@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&family=Sansita+Swashed:wght@500&display=swap);
body,
html {
  height: 100%;
  margin: 0;
}
#root {
  width: 100%;
  height: 100%;
}
.appContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.searchContainer {
  background-image: url(/Weather-App/static/media/Background_Left.3b68cd58.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  height: 100%;
  width: 33.33%;
  justify-content: center;
  align-items: center;
}
.cardContainer {
  background-image: url(/Weather-App/static/media/Background_Right.1eba35f1.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  height: 100%;
  width: 66.67%;
  justify-content: center;
  align-items: center;
}

.inputSearch {
  border: none;
  outline: none;
  border-radius: 1vw;
  padding-left: 1vw;
  width: 20vw;
  height: 2vw;
  box-shadow: 10px 10px 28px 2px rgba(0, 0, 0, 0.75);
  font-family: Sansita Swashed;
  font-size: 1vw;
}

.forecastCardContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 1vw;
        width: 100%;
        height: 100%;
}
.imageForecastCard{
        width: 6vw;
}
.minMaxTemp{
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: .8vw;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.textDay{
        margin-top: .5vw;
        font-size: 1vw;
}
.cardDayContainer {
  background-color: rgba(250, 250, 250, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 25vw;
  height: 100%;
  padding: 1vw;
  box-shadow: 10px 10px 28px 2px rgba(0, 0, 0, 0.75);
  font-family: Oswald;
}
.cityName {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3vw;
  height: 4vw;
  width: 100%;
  margin-bottom: 1vw;
}
.dateContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5vw;
  height: 100%;
  width: 100%;
  margin-bottom: 1vw;
}
.temperatura {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 6vw;
  height: 6vw;
  width: 100%;
  margin-bottom: 1vw;
}
.division {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2vw;
  width: 100%;
  margin-bottom: 1vw;
}

.condition {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2vw;
  height: 2vw;
  width: 100%;
  margin-bottom: 1vw;
}
.minMaxTempCardDay {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3vw;
  width: 100%;
}
.cardForecastContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 1vw;
  width: 100%;
}

